@keyframes text_left {
    0% {
        opacity: 0;
        transform: translateX(-90px);
    }

    100% {
        opacity: 1;
    }
}

@keyframes text_right {
    0% {
        opacity: 0;
        transform: translateX(550px);
    }

    100% {
        opacity: 1;
    }
}

@keyframes text_left_up {
    0% {
        opacity: 0;

    }

    60% {
        opacity: 0;
        transform: translateY(-50px);
    }

    100% {
        opacity: 1;
    }
}

@keyframes text_right_up {
    0% {
        opacity: 0;

    }

    60% {
        opacity: 0;
        transform: translateY(50px);
    }

    100% {
        opacity: 1;
    }
}

@keyframes text_down {
    0% {
        opacity: 0;
    }

    30% {
        opacity: 0;
        transform: translateY(-30px);
    }

    100% {
        opacity: 1;
    }
}

@keyframes text_down1 {
    0% {
        opacity: 0;
    }

    60% {
        opacity: 0;
        transform: translateY(-30px);
    }

    100% {
        opacity: 1;
    }
}

@keyframes animateSphere {
    0% {
        transform: rotateY(0deg)
    }

    100% {
        transform: rotateY(360deg)
    }
}

@keyframes mousedown {
    0%{ opacity: 0;
        transform: translateY(0%);
    }
    100%{
        opacity: 1;
        transform: translateY(30px);
    }
}


body {
